.hero-container {
    display: flex;
}

@keyframes slideIn {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(0);
    }
}

.animated-element1 {
    transform: rotateY(45deg);
    transition: transform 2s ease-in-out;
    animation: rotateY 1500ms infinite alternate ease-in-out 1000ms;

}

.animate1 {
    transform: rotateY(0deg);
}

.hero-title {
    color: #BDBDBD;
    font-weight: 300;
    font-size: 64px;
    text-transform: uppercase;
    animation: slideIn 1s ease-in-out alternate;
}

.hero-title__part {
    color: #333333;
    font-weight: 700;
    font-size: 64px;
}

.button-arrow {
    width: 53px;
    height: 53px;
    padding: 15px;
    border: 1px solid #F2F2F2;

    &:focus,
    &:hover {
        background-color: #F2F2F2;
        cursor: pointer;
    }
}

.button-arrow__img {
    width: 24px;
    height: 24px;
    fill: #333333;
}

.buttons-wrapper {
    margin-top: 90px;
    display: flex;
    gap: 23px;
    align-items: start;
    justify-content: flex-start;
}

.date-wrapper {
    margin-top: 90px;
}

.date-value {
    font-size: 24px;
    color: #BDBDBD;
    letter-spacing: 5px;
}

.img-wrapper {
    position: relative;
    margin-left: auto;
}

.info-button {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 222px;
    height: 71px;
    border: none;
    font-size: 12px;
    letter-spacing: 2.4px;
    text-align: center;
    text-transform: uppercase;
    color: #333333;
    background-color: #FFFFFF;
    align-items: center;
    justify-content: center;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:focus,
    &:hover {
        color: #FFFFFF;
        background-color: #333333;
        cursor: pointer;
    }
}

.button-arrow__btn {
    width: 11px;
    height: 11px;
    fill: #333333;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.info-button:hover .button-arrow__btn,
.info-button:focus .button-arrow__btn {
    fill: #FFFFFF;
}