.container {
    padding-left: 10px;
    padding-right: 10px;
}

@media screen and (min-width:480px) {
    .container {
        width: 480px;
        margin-left: auto;
        margin-right: auto;
    }

}

@media screen and (min-width: 768px) {
    .container {
        width: 768px;
    }

}

@media screen and (min-width: 1200px) {
    .container {
        width: 1200px;

    }

}